import axios from "axios"
import { Suspense, useEffect, useState,useMemo } from "react"
import {useRouteMatch,useHistory} from 'react-router'
import { voteFetcher } from './data-fetcher'
import io from 'socket.io-client'
import uniqBy from 'lodash/uniqBy'
import { useUserInfo } from './UserContext'
import './ViewVote.css'
import { Button } from "antd"
export default function ViewVote(){
    let match = useRouteMatch()
    let history = useHistory()
    // let [voteData,setVoteData] = useState(null)
    let { vote, options } = voteFetcher.read(match.params.id)
    let [optionsInfo, setOptionsInfo] = useState(options)
    useEffect(()=>{
        setOptionsInfo(options)
    },[options])

    let userInfo=useUserInfo()
    //向某个选项投票
    /* id 选项id
    * selected 当前用户是否选中该选项
    */
    async function voteOption(id,selected){
        if (!userInfo) {
            history.push('/login')
            return
          }
        if (!selected) {
            await axios.post('/vote/voteup/' + id)
            console.log('vote ok')
          } else {
            await axios.post('/vote/cancel/' + id)
            console.log('cancel ok')
          }
    }
    useEffect(()=>{
        //没过期 连接
        if(vote.deadline>new Date().toISOString()){
            let socket = io({
                // transports:['websocket','polling']
            })
            socket.emit('select root', vote.id)
            socket.on('voting info',info=>{
                console.log(info)
                setOptionsInfo(info)
            })
            return () => socket.disconnect()
        }

    },[vote.id])

    let allUsers = useMemo(() => {
        let users = optionsInfo.reduce((ary, option) => {
          ary.push(...option.Users)
          return ary
        }, [])
        let uniqUsers = uniqBy(users, 'id')
        return uniqUsers
      }, [optionsInfo])
    return(
        <div>
            <h1>{vote.title}</h1>
            <h2>{vote.desc}{vote.desc?"[多选]":"[单选]"}</h2>
            <div>
            {
                optionsInfo.map((option,idx)=>{
                    let selected=option.Users.some(user=>{return userInfo&&user.id==userInfo.id})
                    return(
                        
                        <div key={idx} className="list">
                            <div className="pin">
                            <div className="warp">
                                <span>{option.content}</span>
                                <span>{selected?'✅':''}</span>
                                <span> ({option.Users.length}票)</span>
                               <span>({(option.Users.length /((allUsers.length*100)==0?1:(allUsers.length))*100).toFixed(2)}%)</span>
                               {
                                !vote.anonymous&&
                                <span>
                                    {
                                        option.Users.map((user,idx)=>{
                                            if(user.avatar){
                                                return <img src={user.avatar} style={{width:30,height:30,borderRadius:'9999px'}}/>
                                            }else{
                                                return <span key={idx} style={{display:"inline-block",width:30,height:30,textAlign:'center',borderRadius:'9999px',border:'1px solid'}}>{user.name.slice(0,1).toUpperCase()}</span>
                                            }
                                            
                                        })
                                    }
                                </span>
                            }
                            </div>
                            
                            </div>
                            <Button  onClick={()=>voteOption(option.id,selected)} className="content">投票</Button>
                            
                        </div>
                    )
                })
            }
            </div>
            <div className="time">{"投票截止:"+(vote.deadline).slice(0,10).concat(" "+ (vote.deadline).slice(11,16))}</div>
        </div>
    )
}