import { NavLink as Link,Switch,Route,Redirect,useHistory} from 'react-router-dom'
import Login from './Login'
import Register from './Register'
import Home from './Home'
import ViewVote from './ViewVote'
import UserContext from './UserContext'
import { Suspense, useState,useEffect } from 'react'
import axios from 'axios'
import CreateVote from './CreateVote'
import My from './My'
import './App.css'
import {useSelector,useDispatch} from 'react-redux'

import { Layout, Button } from 'antd';
const { Content } = Layout;


function App() {
  const history = useHistory()
  const dispatch = useDispatch()
  
  const userInfo = useSelector(state => state.user)


  async function logout(){
    dispatch({type: 'logout'})
  }
  useEffect(()=>{   
    dispatch({type: 'get-user-info'})
},[])
  return (
    <UserContext.Provider value={{userInfo: userInfo}}>
    
    <div className="App">
      {
        userInfo?
        <>
        <div className="foot" >
          {
            userInfo.avatar?
            <img src={userInfo.avatar} className="avatar" alt="头像" />:
            <span className="avatar2">{userInfo.name.slice(0,1).toUpperCase()}</span>
          }
            
              <Button size={"small"} className="logout" onClick={logout}>退出</Button>
            </div>
            <div className="linkWap" >
            <Link to="/home" className="home"  alt="home" >  <img src="./icons/newicon.png" /> <img src="./icons/newgrey.png" /><br />新建</Link>
              <Link to="/My" className="Myvote"  alt="My" ><img src="./icons/myicon.png" /><img src="./icons/mygrey.png" />  <br />我的</Link>
            </div>
        </>
        :
        <>
         <div className="linkWap">
        <Link to="/login" className="login">登录</Link>
        <Link to="/register" className="register">注册</Link>
         </div>   
        </>
      }
   


      <Switch>
      <Route path="/" exact>
        <Redirect to="/home"></Redirect>
      </Route>
        <Route path="/home" component={Home}/>
        <Route path="/login" component={Login}/>
        <Route path="/register" component={Register}/>
        <Route path="/create" component={CreateVote}/>
        <Route path="/my" component={My}/>
        <Route path="/vote/:id">
        <Suspense fallback={'loading...'}>
          <ViewVote />
        </Suspense>
        </Route>
      </Switch>
    </div> 
    </UserContext.Provider>
  );
}

export default App;
