import { useState,useCallback,useEffect} from "react"
import { useLocation, useHistory } from 'react-router-dom'
import axios from 'axios'
import {useSelector,useDispatch} from 'react-redux'
export function useInput(init) {
    let [value, setValue] = useState(init)
    let [checked, setChecked] = useState(init)
  
    let onChange = useCallback(function (e) {
      setValue(e.target.value)
      setChecked(e.target.checked)
    }, [])
  
    return {value, checked, onChange}
  }
  export function useQuery() {
    return new URLSearchParams(useLocation().search);
  }

  export function useRequest(url){
    let [data, setData] = useState(null)
    let [loading, setLoading] = useState(true)
  
    useEffect(() => {
      axios.get(url).then((res) => {
        setData(res.data)
        setLoading(false)
      })
    }, [url])
  
    return [data, loading]
  }
 export function useForceLogin(){
    let history = useHistory()
  let user = useSelector(state => state.user)
  let tried = useSelector(state => state.triedLogin)

  let dispatch = useDispatch()

  useEffect(() => {
    (async () => {
      if (user) {
        return
      }
      if (tried) {
        history.push('/login')
      }
      if (!user) {
        dispatch({type: 'get-user-info'})
      }
    })()
  }, [user, tried])

  return user
  }