import axios from "axios"
import { useEffect, useState } from "react"
import { Link } from "react-router-dom"
import { useForceLogin } from "./hooks"
import { List, InfiniteLoader, AutoSizer} from 'react-virtualized';
import './My.css'
export default function My(){
    let [voteData, setVoteData] = useState({row: [], count: 10})
    let user = useForceLogin()
    let [votes, setVotes] = useState([])

    if (!votes) {
        return <div>loading...</div>
      }


      function loadMore({startIndex, stopIndex}) {
        return axios.get(`/vote/myvotes?startIndex=${startIndex}&stopIndex=${stopIndex}`).then(res => {
          setVoteData(res.data)//里面有总条目数
          votes.splice(startIndex, 0, ...res.data.rows)
          setVotes([...votes])
        }).catch(e => {
          console.log(e)
        })
      }
    
      function rowRenderer({ key, index, style }) {
        // debugger
        if (index >= votes.length) {
          return <div style={style} key={key}>loading...</div>
        }
        return (
          <div style={style} key={key}>
            <Link to={"/vote/" + votes[index].id} className="wrap">{votes[index].title}</Link>
          </div>
        )
      }
    
      function isRowLoaded({ index }) {
        return !!votes[index]
      }
    return (
        <div>
      <InfiniteLoader
        isRowLoaded={isRowLoaded}
        loadMoreRows={loadMore}
        rowCount={voteData.count}
      >
        {({onRowsRendered, registerChild}) => (
          <List
            onRowsRendered={onRowsRendered}
            ref={registerChild}
            width={300}
            height={300}
            rowCount={voteData.count}
            rowHeight={20}
            rowRenderer={rowRenderer}
          />
        )}
      </InfiniteLoader>
    
    </div>
    )
}