import { useEffect, useContext } from 'react'
import { Link,useHistory } from 'react-router-dom'
import UserContext from './UserContext'
import axios from 'axios'
import { useSelector, useDispatch } from 'react-redux'
import {useForceLogin} from './hooks'
import './Home.css'
import { Layout, Button } from 'antd';
const {  Content } = Layout;
export default function Home() {
    let history=useHistory() 
    let user = useSelector(state => state.user)
    let tried = useSelector(state => state.triedLogin)

    let dispatch = useDispatch()

    useEffect(() => {
      (async () => {
        if (user) {
          return
        }
        if (tried) {
          history.push('/login')
        }
        if (!user) {
          dispatch({type: 'get-user-info'})
        }
      })()
    }, [user, tried])
    return(
      <div >
      <Content  className="signalSelect">
      <div><Link to="/create"> <img src="./icons/signal.png"/><br/> <Button className="signal">单选投票</Button></Link></div>
      </Content>
      <Content className="miltSelect">
      <div><Link to="/create?multiSelect">  <img src="./icons/mult.png"/><br/><Button className="signal2">多选投票</Button></Link></div>
      </Content>
    </div>
    )
}
