import {useState,useEffect, useRef, useContext} from 'react'
import axios from 'axios'
import UserContext from './UserContext'
import { useDispatch } from 'react-redux'
import { Form, Input, Button, Checkbox } from 'antd';
import './Login.css'
function Login({history}){
    let usernameRef=useRef()
    let passwordRef=useRef()
    let userCtx=useContext(UserContext) 
    let dispatch = useDispatch()

    async function login() {
      axios.post('/account/login', {
        
        name: usernameRef.current.props.value,
        password: passwordRef.current.props.value,
      }).then(res => {
        dispatch({                                  //dispatch 会立即执行 
          type: 'user-info',
          user: res.data
        })
        history.push("/home")
      }).catch(e => {
        alert(e.toString())
      })
  
    }
  
    return(
      <Form
      name="basic"
      initialValues={{ remember: true }}
      requiredMark={false}  
      className="loginPage"
    >
      <Form.Item
        label="用户名"
        name="username"
        rules={[{ required: true, message: '请输入用户!' }]}
      >
        <Input ref ={usernameRef} autoComplete="off"/>
      </Form.Item>
      <Form.Item
        label="密码"
        name="password"
        rules={[{ required: true, message: '请输入密码!' }]}
      >
        <Input.Password   ref ={passwordRef} />
      </Form.Item>
      <Form.Item >
        <Button className="LogBut" type="primary" htmlType="submit" onClick={login}>
          登录
        </Button>
      </Form.Item>
    </Form>
    )
}

export default Login