import axios from "axios"
import { useState,useRef } from "react"
import { useInput } from "./hooks"
import { Button, Input } from 'antd';
import "./Register.css"
function Register(){
    let nameRef = useRef()
    let passwordRef = useRef()
    let emailRef = useRef()
    let genderM = useRef("")
    let genderF = useRef("")
    let [previewUrl, setPreviewUrl] = useState(null)
    async function register(e){
        e.preventDefault()
      await  axios.post('/account/register',{
        name:nameRef.current.state.value,
        password:passwordRef.current.state.value,
        gender:genderM.current.checked?genderM.current.value:genderF.current.value,
        email:emailRef.current.state.value,
        avatar:previewUrl,
        })
        alert('ok')
    }
    async function upload(e) {
        let fd = new FormData()
        fd.set('file', e.target.files[0])
        let res = await axios.post('/upload', fd)
        setPreviewUrl(res.data.url)
      }
    return(
        <div className="registerWrap" >
        <div className="nameInput"><span>用户名:</span><Input type="text" ref ={nameRef}/></div>
        <div><span>性别:  </span><input type="radio"  name="a1" ref={genderM} value="m"/><span>男</span>
                  <input type="radio"  name="a1" ref={genderF} value="f"/><span>女</span>
        </div>
        <div><span>密码: </span><Input type="password" ref ={passwordRef}/></div>
        <div><span>邮箱：</span><Input type="email" ref ={emailRef}/></div>                   
        <div><span>头像：</span><Input type="file" onChange={upload}/></div>
        <div><img src={previewUrl} style={{width:100,height:100,border:0 }} /> </div> 
        {/* <ImgCrop rotate>
          <Upload
            action='https://www.mocky.io/v2/5cc8019d300000980a055e76'
          
            listType="picture-card"
            fileList={fileList}
            onChange={onChange}
            onPreview={onPreview}
          >
            {fileList.length < 1 && '+ Upload'}
          </Upload>
        </ImgCrop>                       */}
        <Button type="primary" htmlType="submit" onClick={register}>
          注册
        </Button>
        
    </div>
    )
}

export default Register